<template>
    <div>
        <div class="bg" style="background:url(static/img/personal/bg.jpg) no-repeat center center">
        </div>
        <grid-cell :pages="pages"></grid-cell>
    </div>
</template>

<script>
import GridCell from '../../components/GridCell';
import localjson from '../../util/localjson';

export default {
    name: 'personal',
    components: {
        GridCell,
    },
    data() {
        return {
            pages:[
                { index:1, title: '请假考勤', image: 'personal/qjkq.png', path: '/personal/qjkq', isValid: false },
                { index:2, title: '考勤积分', image: 'personal/kqjf.png', path: '/personal/kqjf', isValid: false },
                { index:3, title: '工资条单', image: 'personal/gztd.png', path: '/personal/gztd', isValid: false },
                { index:4, title: '培训教育', image: 'personal/pxjy.png', path: '/personal/pxjy', isValid: false },
                { index:5, title: '网页链接', image: 'personal/wylj.png', path: '/personal/wylj', isValid: true },
                { index:6, title: '便捷工具', image: 'personal/bjgj.png', path: '/personal/bjgj', isValid: false },
            ],
        };
    },
    methods: {
    },
    mounted() {
        const currpage = {text: '个人事务', route: '/personal'};
        localjson.setCurrentPage(currpage);
        this.$store.commit('setCurrentPage', currpage);

        this.$store.commit('setAppHeader', {
            show: true,
            title: '个人事务',
            showMenu: true,
            showBack: false,
            actions: [
            ]
        });
        this.$store.commit('setEnableSwipeOut', true);
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.bg
    
    margin 0 auto
    width 100%
    height 180px

</style>



